body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.Box {
	width: calc(25% - 40px);
	height: 100px;
	background-color: red;
	margin: 20px;
	float: left;
	box-sizing: border-box;
	text-align: center;
}

.boxText {
	line-height: 60px;
}

@media screen and (max-width: 900px) {
	.Box {
		width: calc(50% - 40px);
	}
}
.BoxButton {
	width: 50%;
	height: 40px;
	box-sizing: border-box;
	cursor: pointer;
}
.Header {
	width: 100%;
	height: 50px;
	background-color: blue;
}

.HeaderButton {
	width: 100px;
	height: 30px;
	margin: 10px 20px;
	box-sizing: border-box;
	float: left;
	background-color: green;
	cursor: pointer;
}

.AdminBox {
	width: calc(25% - 40px);
	height: 100px;
	background-color: yellow;
	margin: 20px;
	float: left;
	box-sizing: border-box;
	text-align: center;
}

.AdminBoxText {
	line-height: 60px;
}

@media screen and (max-width: 900px) {
	.AdminBox {
		width: calc(50% - 40px);
	}
}
.AdminBoxButton {
	width: 50%;
	height: 40px;
	box-sizing: border-box;
	cursor: pointer;
}
